.slick-dots{
    position: absolute;
    bottom: -40px;
    left: 0;
    z-index: 999;
}
.slick-dots li button:before{
    color: #fff;
}
.slick-dots li.slick-active button:before {
    color: #fff;
    opacity: 1;
}
.slick-prev::before, .slick-next::before {
    color: #fff;
}
.slick-slider .slick-track, .slick-slider .slick-list {
    border-radius: 15px;
}
.slick-arrow{
    position: absolute;
    top: 50%;
    left: 50%;
transform: translate(-50%,-50%);
    z-index: 999;
}
.slick-prev {
    margin: 0 0 0 -670px;
}
.slick-next {
    margin: 0 0 0 650px;
}
.slick-prev::before,.slick-next::before{
    font-size: 35px;
}
.flex_box {
    display: flex;
    border-radius: 15px;
}
.left_box {
    width: 600px;
    padding: 60px 0 0 60px;
    border-right: 1px solid #ddd;
    position: relative;
}
.left_box h2{
    font-size: 55px;
    font-weight: 500;
    color: #3182f6;
    margin-bottom: 30px;
}
.left_box .kakao{
    color: #fee102;
}
.left_box .AstonMartin{
    color: #247d54;
}
.left_box .Lotte{
    color: #da291c;
}
.left_box .shinsegae{
    color: #e60000;
}
.left_box .Info h3{
    font-size: 24px;
    font-weight: 300;
    color: #343434;
    margin-bottom: 20px;
}
.left_box .Info ul{
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 40px;
    line-height: 25px;
}
.left_box .Info li~li {
    position: relative;
    margin-left: 10px;
}
.left_box .Info li:nth-child(1){
    margin-bottom: 10px;
}
.left_box .Info li strong{
    background: #3182f6;
    color: #fff;
    border-radius: 2px;
    padding: 1px;
}
.left_box .Info li .kakao{
    background: #fee102;
}
.left_box .Info li .AstonMartin{
    background: #247d54;
}
.left_box .Info li .Lotte{
    background: #da291c;
}
.left_box .Info li .shinsegae{
    background: #e60000;
}
.left_box .Info li~li::before{
    content: "";
    position: absolute;
    inset: 12px auto auto -8px;
    transform: translate(0, -50%);
    width: 3px;
    height: 3px;
    background: #555;
    border-radius: 50%;
    margin-right: 5px;
    vertical-align: middle;
}
.left_box .Info p{
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 60px;
    line-height: 25px;

}
.Link {
position: absolute;
bottom: 15%;
left: 40%;
}
.Link ul {
    display: flex;
    gap: 20px;
}
.Link ul li a {
    padding: 12px 25px 12px 25px;
    border: 2px solid #3182f6;
    background: #3182f6;
    color: #fff;
    transition: 0.5s;
}

.Link ul li a:hover {
    background: #fff;
    color: #000;
    border: 2px solid #3182f6;
}
.kakao_link ul li a {
    padding: 12px 20px 12px 20px;
    border: 2px solid #fee102;
    background: #fee102;
    color: #fff;
    transition: 0.5s;
}

.kakao_link ul li a:hover {
    background: #fff;
    color: #000;
    border: 2px solid #fee102;
}
.AstonMartin_link ul li a {
    padding: 12px 20px 12px 20px;
    border: 2px solid #247d54;
    background: #247d54;
    color: #fff;
    transition: 0.5s;
}

.AstonMartin_link ul li a:hover {
    background: #fff;
    color: #000;
    border: 2px solid #247d54;
}
.Lotte_link ul li a {
    padding: 12px 20px 12px 20px;
    border: 2px solid #da291c;
    background: #da291c;
    color: #fff;
    transition: 0.5s;
}

.Lotte_link ul li a:hover {
    background: #fff;
    color: #000;
    border: 2px solid #da291c;
}
.shinsegae_link ul li a {
    padding: 12px 20px 12px 20px;
    border: 2px solid #e60000;
    background: #e60000;
    color: #fff;
    transition: 0.5s;
}

.shinsegae_link ul li a:hover {
    background: #fff;
    color: #000;
    border: 2px solid #e60000;
}
.right_box {
    width: 600px;
}
img{
    width: 100%;
}
.right_box .content {
    opacity: 1;
    height: 600px;
    overflow: hidden;
    align-items: flex-start;
   
}

.right_box .content .case {
    transform: translate(0, 0);
    transition: 2s;
    border-radius: 15px;
}

.right_box .content:hover .case {
    transform: translate(0, calc(-100% + 600px));
    border-radius: 15px;
}
@media (max-width: 767.98px) {
    .flex_box {
        flex-direction: column;
    }

    .left_box,
    .right_box {
        width: 100%;
    }

    .left_box {
        padding: 30px;
        border-right: none;
        border-bottom: 1px solid #ddd;
        text-align: center;
        height: 600px;
        letter-spacing: 0.1em;
    }

    .left_box h2 {
        font-size: 50px;
        margin-bottom: 20px;
        margin-top: 70px;
    }

    .left_box .Info h3 {
        font-size: 18px;
        margin-bottom: 10px;
    }

    .left_box .Info ul {
        font-size: 14px;
        margin-bottom: 20px;
        line-height: 20px;
    }

    .left_box .Info li~li {
        margin-left: 5px;
    }

    .left_box .Info li:nth-child(1) {
        margin-bottom: 5px;
    }

    .left_box .Info p {
        font-size: 14px;
        margin-bottom: 30px;
        line-height: 20px;
    }

    .Link {
        position: static;
        margin-bottom: 15px;
    }

    .Link ul {
        justify-content: center;
    }

    .slick-arrow {
        display: none;
    }

    .slick-prev {
        margin: 0;
    }

    .slick-next {
        margin: 0;
    }

    .right_box .content {
        display: none;
    }
.slick-prev::before, .slick-next::before{
    display: none;
}
    .right_box .content:hover .case {
        transform: translate(0, 0);

    }
    .left_box .Info li~li::before {
        display: none;
    }
}